
.demo-container {
  -webkit-transition: max-height 0.3s ease;
  transition: max-height 0.3s ease;
  overflow: hidden;
}
.docs-demo {
  width: 100%;
  height: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px;
  background-color: #F7F7F7;
  border: 1px solid #e2ecf4;
  border-top: none;
}
.docs-demo pre code {
  font-family: Consolas,Menlo,Courier,monospace;
  line-height: 22px;
  border: none;
}
.docs-trans {
  width: 100%;
  text-align: center;
  display: inline-block;
  color: #C5D9E8;
  font-size: 12px;
  padding: 10px 0;
  background-color: #FAFBFC;
}
.docs-demo__code,
.highlight-wrapper,
.docs-demo__meta {
  padding: 0 20px;
  overflow-y: auto;
}
.docs-demo__code {
  border-bottom: 1px solid #eee;
}
.docs-demo.docs-demo--expand .docs-demo__meta {
  border-bottom: 1px dashed #e9e9e9;
}
.docs-demo.docs-demo--expand .docs-demo__triangle {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.highlight-wrapper {
  display: none;
}
.highlight-wrapper p,
.highlight-wrapper pre {
  margin: 0;
}
.highlight-wrapper .hljs {
  padding: 0;
}
.docs-demo.docs-demo--expand .highlight-wrapper {
  display: block;
}
.docs-demo__code__mobi {
  height: 620px;
  margin: 20px 0;
}
.docs-demo__code__mobi__header {
  border-radius: 4px 4px 0 0;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(55, 55, 55, 0.98)), to(#545456));
  background: linear-gradient(rgba(55, 55, 55, 0.98), #545456);
  text-align: center;
  padding: 8px;
}
.docs-demo__code__mobi__header img {
  width: 100%;
}
.docs-demo__code__mobi__header .url-box {
  height: 28px;
  line-height: 28px;
  color: #fff;
  padding: 0 3px;
  background-color: #a2a2a2;
  margin: 10px auto 0;
  border-radius: 4px;
  white-space: nowrap;
  overflow-x: auto;
}
.docs-demo__code__mobi__content iframe {
  width: 100%;
  border: 0;
  height: 548px;
}

body,
div,
span,
p,
a,
img,
input,
ol,
ul,
li,
h1,
h2,
h3,
h4,
strong,
em,
i,
del {
	margin: 0;
	padding: 0;
	/* 清除标签的内外边距 */
}
a {
	text-decoration: none;
	color: #000;
	/* 清除a标签的默认样式 */
}
input {
	border: 0 none;
	outline: 0 none;
	/* 清除input的边框以及外轮廓线 */
}
img {
	border: 0 none;
	/* 在低版本的浏览器中,img是自带边框的 */
	vertical-align: middle;
	/* 图片与文字中线对齐 */
}
li {
	list-style: none;
	/* 清除li标签自带的小点点 */
}
h1,
h2,
h3,
h4,
strong {
	font-weight: normal;
}
em,
i {
	font-style: normal;
}
body {
	background-color: #fff;
	font: 12px/100% "\5FAE\8F6F\96C5\9ED1";
}
.clearfix:after {
	content: ".";
	display: block;
	clear: both;
	height: 0;
	visibility: hidden;
}
.clearfix {
	*zoom: 1;
}

.router {
  width: 200px;
  height: 200px;
  overflow: auto;
  float: left;
  background-color: skyblue;
}
.main {
  float: left;
  width: -140%;
  height: 1000px;
  overflow: auto;
}

.navigation {
  width: 1340px;
  height: 75px;
  border-bottom: 2px solid blue;
  margin: 10px auto 0px;
}
.navigation h1 {
  float: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 12px;
}
.navigation h1 .logo {
  width: 50px;
  height: 50px;
  background: url('/static/static/logo.jpg') 100% 100%;
  background-size: 100% 100%;
  float: left;
  margin-right: 30px;
}
.navigation h1 div {
  float: left;
}
.navigation h1 div:nth-child(2):hover {
  cursor: pointer;
  color: blue;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transition: all 1s;
  transition: all 1s;
}
